<template>
  <div>
    <div class="m-container">
      <view-header title="医院介绍"></view-header>
      <div class="box" v-if="isHave === false">
        <div class="header">
          <p class="title">医院介绍</p>
          <img class="edit-icon" src="static/img/edit-icon.png" v-if="input.length !== 0" @click="dialogVisible1 = true"/>
        </div>
        <div class="add" @click="_queryHospital()" v-if="input.length === 0">
          <img class="icon" src="static/img/add-icon.png"/>
          <p class="tip">点击添加医院信息</p>
        </div>
        <div class="add" v-else>
          <div class="content">
            <div class="left">
              <div class="icon"></div>
            </div>
            <div class="right">
              <p class="item">{{input}}</p>
              <p class="item">{{input}}</p>
              <p class="item">{{input}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="box-later" v-else>
        <div class="item flex flex-end">
          <img class="edit-icon" src="static/img/edit-icon.png" @click="dialogVisible1 = true">
        </div>
        <div class="item-half">
          <p style="color: #1AC7D8">医院名称:</p>
          <p style="margin: 15px 0">{{hospitalName}}</p>
        </div>
        <div class="item-half">
          <p style="color: #1AC7D8">医院等级:</p>
          <p style="margin: 15px 0">{{hospitalLevel}}</p>
        </div>
         <div class="item-half">
            <p style="color: #1AC7D8">医院官网:</p>
           <p style="margin: 15px 0">{{hospitalSite}}</p>
        </div>
         <div class="item-half">
            <p style="color: #1AC7D8">医院电话 </p>
           <p style="margin: 15px 0">{{hospitalTel}}</p>
        </div>
         <div class="item-half">
            <p style="color: #1AC7D8">医院地址:</p>
           <p style="margin: 15px 0">{{hospitalAddress}}</p>
        </div>
        <div class="item-half">
          
          <p style="color: #1AC7D8">交通方式:</p>
          <p>{{hospitalTra}}</p>
        </div>
        <div class="item-half">
          <p style="color: #1AC7D8">医院简介:</p>
          <p>{{hospitalSum}}</p>
        </div>
      </div>
    </div>
    <el-dialog
      title="医院介绍"
      :visible.sync="dialogVisible1"
      width="80%">
      <div class="dialog">
        <div class="item-half">
          <p><span>*</span>医院名称:</p>
          <el-input v-model="hospitalName"  readonly></el-input>
        </div>
        <div class="item-half">
          <p><span>*</span>医院等级:</p>
          <el-input v-model="hospitalLevel" placeholder="请输入内容"></el-input>
        </div>
        <div class="item-half">
          <p><span>*</span>官方网站:</p>
          <el-input v-model="hospitalSite" placeholder="请输入内容"></el-input>
        </div>
        <div class="item-half">
          <p><span>*</span>医院电话:</p>
          <el-input v-model="hospitalTel" placeholder="请输入内容"></el-input>
        </div>
        <div class="item-half">
          <p><span>*</span>医院地址:</p>
          <el-input v-model="hospitalAddress" placeholder="请输入内容"></el-input>
        </div>
        <div class="item-half">
          <p>医院交通:</p>
          <el-input placeholder="请输入内容" v-model="hospitalTra">
          </el-input>
        </div>
        <div class="item-half">
          <p>医院介绍:</p>
          <el-input
            type="textarea"
            rows="5"
            resize="none"
            placeholder="请输入内容"
            v-model="hospitalSum">
          </el-input>
        </div>
        <!-- <div class="item-half">
          <p>上传医院图片:</p>
          <p>尺寸要求：不小于800*480（480*800），单张图片不能超过2M。</p>
          <div class="image-upload">
            <div class="logo"></div>
            <input class="upload-input" type="file"/>
          </div>
        </div>
        <div class="item-half">
          <p>上传医院Logo:</p>
          <p>尺寸要求：不小于800*480（480*800），单张图片不能超过200KB。</p>
          <div class="image-upload">
            <div class="logo"></div>
            <input class="upload-input" type="file"/>
          </div>
        </div> -->
      </div>
      <footer class="footer">
        <button class="btn" @click="_updateHospital()">保存</button>
      </footer>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
import ViewHeader from 'base/view-header/view-header'
import {queryhospital,updatehospital} from '@/api'
export default {
  components: {
    ViewHeader
  },
   mounted () {
    var _this = this
    var params = new URLSearchParams()
    queryhospital(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
         _this.hospitalLevel = data.data.level
         _this.hospitalSite = data.data.website
         _this.hospitalTel = data.data.telephone
         _this.hospitalTra = data.data.traffic
         _this.hospitalSum = data.data.summary
         _this.hospitalAddress = data.data.address
        }
      }).catch(function (data) {
        alert(data)
      })
  },
  data () {
    return {
      dialogVisible1: false,
      input: '',
      isHave: true,
      hospitalName: '东方医院',
      hospitalLevel:'',
      hospitalSite:'',
      hospitalTel:'',
      hospitalTra:'',
      hospitalSum:'',
      hospitalAddress:'',
      districtCode: []
    }
  },
   methods: {
     _updateHospital () {
        var _this2 = this
        var params = new URLSearchParams()
        params.append('hospitalName', _this2.hospitalName)
        params.append('hospitalLevel', _this2.hospitalLevel)
        params.append('hospitalSite', _this2.hospitalSite)
        params.append('hospitalTel', _this2.hospitalTel)
        params.append('hospitalTra', _this2.hospitalTra)
        params.append('hospitalSum', _this2.hospitalSum)
        params.append('hospitalAddress', _this2.hospitalAddress)

        updatehospital(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
          _this2.dialogVisible1= false
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
     }
    // _queryHospital () {
    // var _this = this
    // var params = new URLSearchParams()
    // queryhospital(params).then(function (data) {
    //     if (!data.success) {
    //       alert(data.error)
    //     } else {
    //      _this.hospitalLevel = data.data.level
    //      _this.hospitalSite = data.data.website
    //      _this.hospitalTel = data.data.telephone
    //      _this.hospitalTra = data.data.traffic
    //      _this.hospitalSum = data.data.summary
    //      _this.hospitalAddress = data.address
    //      _this.dialogVisible1 = true
    //     }
    //   }).catch(function (data) {
    //     alert(data)
    //   })
    //  }
   }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-column
  width: 100%
  height: 100%
  padding: 0 10px
  .box
    width: 100%
    border: 1px solid $color-back-gray_2
    padding: 20px
    background-color: $color-back-white_0
    .header
      @extend .flex
      @extend .flex-row
      @extend .flex-between
      .title
        font-size: $size-16
        color: $color-back-black_1
      .edit-icon
        @extend .pointer
        width: 32px
        height: 32px
    .add
      @extend .flex
      @extend .flex-column
      @extend .flex-center
      width: 100%
      height: 160px
      .icon
        @extend .pointer
        width: 40px
        height: 40px
      .tip
        font-size: $size-14
        color: $color-back-black_1
        margin-top: 20px
      .content
        @extend .flex
        @extend .flex-row
        width: 100%
        .left
          @extend .flex
          @extend .flex-center
          width: 200px
          .icon
            @extend .pointer
            width: 120px
            height: 120px
            border-radius: 60px
            padding: 40px
            border: 1px solid $color-back-blue_0
        .right
          @extend .flex
          @extend .flex-column
          @extend .flex-center
          width: calc(100% - 200px)
          .item
            width: 100%
            margin-bottom: 20px
            font-size: $size-14
            color: $color-back-black_1
  .box-later
    @extend .flex
    @extend .flex-row
    @extend .flex-between
    @extend .flex-top
    flex-wrap: wrap
    width: 100%
    border: 1px solid $color-back-gray_2
    padding: 20px
    background-color: $color-back-white_0
    .item
      width: 100%
      .edit-icon
        @extend .pointer
        width: 32px
        height: 32px
    .item-half
      @extend .flex
      @extend .flex-column
      width: 48%
      padding-bottom 20px
      p
        margin-bottom 10px
      .image
        width: 100%
        height 200px
.dialog
  @extend .flex
  @extend .flex-row
  @extend .flex-between
  flex-wrap: wrap
  width: 100%
  .item
    width: 100%
  .item-half
    width: 48%
  .item
  .item-half
    margin-bottom: 20px
    span
      color: $color-back-blue_2
    .image-upload
      @extend .flex-center
      width 80px
      height 80px
      border 1px solid $color-back-gray_4
      position relative
      .logo
        width: 40px
        height: 40px
        background-size 100% 100%
        background-image url('/static/img/add-icon.png')
      .upload-input
        position absolute
        width: 100%
        height 100%
        top: 0px
        left: 0px
        opacity: 0
.footer
  @extend .flex
  @extend .flex-end
  width: 100%
.btn
  border: 0px
  background-color: $color-back-blue_0
  color: $color-back-white_0
  font-weight: bolder
  padding: 0 35px
  margin-left: 10px
  height: 40px
  font-size: $size-16
</style>



